* {
  margin: 0;
}

.app {
  background-color: black;
  height: 100vh;
}

.container {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
}

img {
  align-self: center;
  width: 50vw;
}

.container label {
  align-self: center;
}

#name-filter {
  background-color: white;
  border-radius: 20px;
  font-size: 28px;
  margin-bottom: 50px;
  padding: 5px 10px;
}

#name-filter:hover {
  border: 2px solid yellow;
}
